<template>
  <div>{{ $route.params.id }}</div>
</template>
<script>
  export default {
    name: 'Child',
    created () {
      console.log(this.$route.params.id)
    },
    beforeRouteEnter (to, from, next) {
      console.log('........beforeRouteEnter........')
      next()
    },
    beforeRouteUpdate (to, from, next) {
      console.log('........beforeRouteUpdate........')
      next()
    }
  }
</script>



